
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'ListItem',
  props: {
    item: {
      type: Object, 
      required: true
    }
  },
  methods: {
    setClickData(id: number, name: string) {
      this.$store.dispatch('peoplesAction', {num: id, name: name})
    }
  }
})
